
.navbar{
    width: 100%;
    height: 100px;
    margin-bottom: 5px;
    display: flex;
    align-items: center;
    overflow: hidden;
}
.navbar-content{
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
}
.navbar-home-img{
    display: flex;
    align-items: center;
    height: 100%;
    width: 100%;
    margin-left: 100px;
    margin-top: 30px;
}
.navbar-home-img .logo{
    width: 60px;
    height: 50px;
    cursor: pointer;
    margin-top: 3px;
}
.navbar-say-hello{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    height: 100%;
    margin-right: 100px;
    margin-top: 30px;
    
}
.linkedin{
    padding: 11px 19px 11px 19px;
    margin-right: 5px;
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-size: 18px;
    text-decoration: none;
    color: #333;
}
.linkedin:hover{
    color: #0080ff;
}
.say-hello{
    border: 2px solid #0080ff;
    padding: 11px 19px 11px 19px;
    border-radius: 29px;
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-size: 18px;
    text-decoration: none;
    color: #0080ff;
}
.say-hello:hover {
    background-color: #0080ff;
    color: #fff;
    transform: scale(1.1);
    transition: 0.3s;
}
@media only screen and (max-width: 1190px){
    .navbar-home-img{
        margin-left: 80px;
        transition: 0.5s;
    }
}

@media only screen and (max-width: 1090px){
    .navbar-home-img{
        margin-left: 60px;
        transition: 0.5s;
    }
}
@media only screen and (max-width: 990px){
    .navbar-home-img{
        margin-left: 20px;
        transition: 0.5s;
    }
}
@media only screen and (max-width: 890px){
    .navbar-home-img{
        margin-left: 20px;
        transition: 0.5s;
    }
    .say-hello{
        margin-right: -40px;
        transition: 0.5s;
    }
}
@media only screen and (max-width: 790px){
    .navbar-home-img{
        margin-left: 15px;
        transition: 0.5s;
    }
    .say-hello{
        margin-right: -80px;
        font-size: 15px;
        transition: 0.5s;
    }
}
@media only screen and (max-width: 600px){
    .say-hello{
        margin-right: -90px;
        font-size: 13px;
        text-align: center;
        transition: 0.5s;
    }
    .linkedin{
        display: none;
    }
}