.section-content2{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
  }
.section-card{
    margin-top: -120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 100%;
    border-radius: 25px;
    border: 2px solid #ddd;
    box-shadow: 0px 0px 10px 5px rgba(0,0,0,0.1);
}
.section-card img{
    width: 70px;
    height: 70px;
    margin-top: 70px;
    color: #0040ff;
}
.card-content{
    display: flex;
    justify-content: center;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    width: 80%;
    height: 60%;
    margin-top: 20px;
}
.card-content h1{
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-size: 20px;
    color: #141c3a;
    letter-spacing: 0.2rem;
    text-align: center;
    padding: 20px;
    width: 100%;
}
.card-content p{
    width: 100%;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 8px;
    text-align: center;
    font-family: Roboto, sans-serif;
    font-weight: 300;
    font-size: 15px;
    color: #141c3a;
}
.card-content h2{
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-size: 15px;
    color: #0040ff;
    margin-top: 10px;
    margin-bottom: 10px;
}
@media only screen and (max-width: 800px){
  .section-card img{
    width: 40px;
    height: 40px;
    margin-top: 5px;
    margin-bottom: -20px;
  }
  .card-content h1{
    font-size: 19px;
    padding-bottom: 11px;
  }
  .card-content h2{
    margin: 2px;
    font-size: 14px;
  }
  .card-content p{
    padding: 8px;
    font-size: 11px;
  }
  .section-card{
    margin-top: -60px;
  }
}
@media only screen and (max-width: 450px){
  .section-content2{
    height: 70vh;
  }
  .section-card img{
    width: 50px;
    height: 50px;
    margin-top: 5px;
    margin-bottom: -20px;
  }
  .card-content h1{
    font-size: 19px;
    padding-bottom: 11px;
  }
  .card-content h2{
    margin: 2px;
    font-size: 14px;
  }
  .card-content p{
    padding: 8px;
    font-size: 11px;
  }
}
@media only screen and (max-height: 600px){
  .section-content2{
    height: 190vh;
  }
  .section-card{
    margin-top: -90px;
  }
}
@media only screen and (max-height: 350px){
  .section-content2{
    height: 230vh;
  }
  .section-card{
    margin-top: -60px;
  }
}