.works-section{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    min-height: 130vh;
}
.work-h1{
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-size: 35px;
    color: #141c3a;
    text-align: center;
    padding: 10px;
    margin-bottom: 30px;
}
.work-p{
    font-family: Roboto, sans-serif;
    font-weight: 300;
    font-size: 20px;
    color: #141c3a;
    text-align: center;
}
.work-a{
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-size: 18px;
    text-decoration: none;
    color: #0040ff;
}
.works-container{
    position: relative;
    width: 940px;
    height: 100vh;
    margin: 20px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    grid-gap: 60px;
    justify-items: center;
    align-items: center;
}
.hola{
    display: flex;
    flex-direction: row;
}
.works-container .box{
    display: grid;
    justify-items: center;
    align-items: center;
    position: relative;
    width: 100%;
    height: 200px;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 25px;
    margin-bottom: 20px;
}
.item{
    display: grid;
    justify-items: center;
    align-items: center;
    position: relative;
    background-color: #141c3a;
    width: 280px;
    height: 200px;
    margin: 5px;
    padding: 10px;
    border-radius: 25px;
}
.item::before{
    content: '';
    position: absolute;
    width: 60%;
    bottom: -55px;
    height: 40px;
    background: linear-gradient(rgba(0,0,0,0.20), transparent, transparent);
    border-radius: 50%;
    transition: 0.5s;
}
.item:hover::before{
    opacity: 0.5;
    transform: scale(0.8);
}

.item:hover{
    transform: translateY(-5px);
    transition: 0.5s;
}
.item img{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.9);
    border-radius: 25px;
    object-fit: cover;
    cursor: pointer;
}
.item-content{
    background-color: #141c3a;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    border-radius: 25px;
    transition: 0.5s;
    opacity: 0;
    text-align: center;
}
.item-content p{
    background-color: #141c3a;
    padding: 25px;
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-size: 14px;
    color: #fff;
    letter-spacing: 2px;
    border-radius: 25px;
    text-align: center;
}
.item-content a{
    text-decoration: none;
    background-color: #141c3a;
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-size: 13px;
    color: #fff;
    padding: 15px 20px 15px 20px;
    border: 3px solid #0040ff;
    border-radius: 25px;
    position: relative;
    cursor: pointer;
}

.item-content a:hover{
    background-color: #0040ff;
    transition: 0.7s;
}

.item-content:hover{
    opacity: 1;
    z-index: 999;
    transition: 0.5s;
}
@media only screen and (max-width: 800px){
    .works-container .box{
        margin-bottom: 10px;

    }
    .works-container{
        max-width: 400px;
    }
    .item{
        width: 220px;
        height: 140px;
    }
    
}
@media only screen and (max-width: 550px){
    .works-section{
        min-height: 180vh;
    }
    .work-h1,
    .work-p,
    .work-a{
        font-size: 14px;
    } 
    .item-content p{
        font-size: 11px;
    }
    .item-content a{
        font-size: 11px;
    }
    .works-container{
        max-width: 300px;
        display: flex;
        flex-direction: column;
        padding: 0px;
    }
}
@media only screen and (max-width: 450px){
    .works-section{
        min-height: 170vh;
        margin-top: 0px;
    }
    .works-container{
        min-height: 150vh;
        max-width: 90%;

    }
}
@media only screen and (max-height: 600px){
    .works-section{
        margin-top: -70px;
        min-height: 200vh;
    }
    .works-container{
        min-height: 150vh;
        max-width: 90%;
        margin: 0;
        padding: 0;
    }
}
@media only screen and (max-height: 350px){
    .works-section{
        margin-top: -40px;
        min-height: 280vh;
    }
    .works-container{
        min-height: 180vh;
        max-width: 90%;
        margin: 0;
        padding: 0;
    }
}


