.section-content1{
    background-color: #4da6ff ;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}
.section-content1 h1{
    background-color: #4da6ff;
    width: 60%;
    color: #fff;
    font-family: Roboto, sans-serif;
    font-weight: 600;
    font-size: 28px;
    margin-bottom: 20px;
    text-align: center;
    text-shadow: 0px 1px 1px #000;
}
.section-content1 p{
    background-color: #4da6ff ;
    margin-top: 40px;
    width: 60%;
    color: #fff;
    text-align: center;
    font-family: Roboto, sans-serif;
    font-weight: 300;
    font-size: 20px;
    letter-spacing: 2px;
}

@media only screen and (max-width: 800px){
    .section-content1 h1{
        margin-top: -20px;
        margin-bottom: 0px;
    }
    .section-content1 p{
        width: 90%;
        font-size: 12px;
    }
}
@media only screen and (max-width: 600px){
    .section-content1 h1{
        margin-top: -20px;
        margin-bottom: 0px;
    }
    .section-content1 p{
        width: 90%;
        font-size: 12px;
    }
}
@media only screen and (max-width: 450px){
    .section-content1{
        height: 80vh;
    }
    .section-content1 p{
        font-size: 16px;
    }
}
@media only screen and (max-height: 600px){
    .section-content1 h1{
        margin-top: -20px;
        margin-bottom: 0px;
    }
    .section-content1 p{
        width: 70%;
        font-size: 16px;
    }
}