.hero-section{
    width: 100%;
    height: 140vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 40px;
    overflow: hidden;
  }
.hero-tittle{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.hero-tittle h1{
    text-align: center;
    font-family: Roboto, sans-serif;
    font-weight: 600;
    font-size: 50px;
    color: #141c3a;
    letter-spacing: 4px;
    padding: 20px;
    margin-top: 10px;
}
.hero-tittle h2{
    text-align: center;
    font-family: Roboto, sans-serif;
    font-weight: 300;
    font-size: 20px;
    color: #141c3a;
    margin-top: 10px;
}
.hero-tittle img{
    justify-content: center;
    display: flex;
    width: 240px;
    height: 260px;
    border-radius: 50%;
    background-color: #4da6ff;
    margin-top: 50px;
}
.hero-footer{
    display: flex;
    justify-content: center;
    width: 100%;
    height: 400px;
}
.hero-footer img{
    background-color: transparent;
    width: auto;
    height: 390px;
    margin-top: 72px;
    filter: drop-shadow(0 0 3px#0010ff);
}
@media only screen and (max-width: 900px){
    .hero-footer img{
        margin-top: 130px;
        width: auto;
        height: 320px;
        transition: 0.5s;
      }
      .hero-footer img{
        display:none;
      }
}

@media only screen and (max-width: 800px){
  .hero-tittle h1{
    font-size: 35px;
    margin-top: 100px;
    transition: 0.5s;
  }
  .hero-tittle img{
    width: 190px;
    height: 210px;
    transition: 0.5s;

  }
  .hero-footer img{
    display:none;
  }
}
@media only screen and (max-width: 500px){
  .hero-section{
    height: 60%;
    margin-bottom: 20px;
  }
  .hero-footer img{
    margin-top: 185px;
    width: auto;
    height: 210px;
  }
  .hero-footer{
    display: none;
  }
}
@media only screen and (max-height: 550px){
  .hero-section{
    min-height: 170vh;
  }
}