footer{
    width: 100%;
    height: 30vh;
    background-color: #4da6ff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    text-align: center;
}
.footer-img{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    background-color: #4da6ff;
}
.footer-img a{
    background-color: #4da6ff;
}
.footer-img .logo{
    width: 60px;
    height: 50px;
    cursor: pointer;
    margin: 0;
    background-color: #4da6ff;
}
.footer-img .logo:hover{
   transform: scale(1.3);
   transition: 0.5s;
}
.footer-img p{
    padding: 10px;
    font-family: Roboto, sans-serif;
    font-weight: 500;
    font-size: 17px;
    background-color: #4da6ff;
    color: #fff;
}
.social-media-a{
    background-color: #4da6ff;
    justify-content: center;
    align-items: center;
    font-size: 30px;
}
i{
    background-color: #4da6ff;
    color: #fff;
    margin: 10px;
    padding: 10px
}
i:hover{
    outline: none;
    color: #0a0c5a;
    text-shadow: 0px 1px 5px rgba(0, 0, 0, 0.5);
    transform: scale(1.4);
    transition: 0.5s;
}
@media only screen and (max-width: 800px){
    footer{
        height: 50vh;
    }
}
@media only screen and (max-width: 600px){
    footer{
        height: 30vh;
        margin-top: 120px;
    }
    .footer-img .logo{
        width: 50px;
        height: 40px;
        margin-top: 25px;
    }
    i{
        margin-top: -15px;
    }
}
@media only screen and (max-width: 500px){
    footer{
        height: 30vh;
        margin-top: 120px;
    }
    .footer-img .logo{
        width: 45px;
        height: 35px;
        margin-top: 5px;
    }
    .footer-img a{
        font-size: 18px;
        margin: 0px;
    }
    .footer-img p{
        font-size: 12px;
    }
}
@media only screen and (max-height: 550px){
    footer{
        height: 60vh;
    }
}