*{
    margin:0px;
    padding:0px;
    background-color:#fff;

}
html{
    scroll-behavior: smooth;
    font-family: "europa", sans-serif;
}
::-webkit-scrollbar {
    width: 6px;
}
  
  /* Track */
::-webkit-scrollbar-track {
    background: #fff;
}
  
  /* Handle */
::-webkit-scrollbar-thumb {
    background: #0080ff;
}
  
  /* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #001427;
}