.container{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    animation: translate-up 1s ease-in-out;
    animation: translate-down 1s ease-in-out;
}
.hero-container{
    border-top: solid 1px #ddd;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin-top: 20px;
}
.hero-image img{
    background-color: #0080ff;
    margin-top: -45px;
    border-radius: 50%;
    width: 80px;
    height: 80px;
}
.tittle-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    width: 60%;
}
.tittle-container h1{
    padding: 5px;
    text-align: center;
    font-family: Roboto, sans-serif;
    font-weight: 300;
    font-size: 40px;
    color: #141c3a;
}
.form-section{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}
.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}
.form-input{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: 60%;
    height: 20%;
}
.form-input-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}
.form-input-item label{
    font-size: 19px;
    font-family: Roboto, sans-serif;
    font-weight: 500;
    margin-bottom: 10px;
    color: #777;
}
.form-input-item input{
    width: 100%;
    height: 4rem;
    padding-left: 20px;
    border-style: none;
    border: 1px solid #E6ECF8;
    outline: none;
    font-size: 1.8rem;
    font-family: Roboto, sans-serif;
    font-weight: 300;
}
.input-email{
    margin-left: 30px;
}
.textarea{
    display: flex;
    flex-direction: column;
    width: 60%;
    height: 40%;
}
.textarea label{
    font-size: 19px;
    font-family: Roboto, sans-serif;
    font-weight: 500;
    margin-bottom: 10px;
    margin-top: 30px;
    color: #777;  
}
.textarea textarea{
    min-width: 20%;
    height: 190px;
    border-style: none;
    border: 1px solid #E6ECF8;
    outline: none;
    font-size: 1.8rem;
    font-family: Roboto, sans-serif;
    font-weight: 300;
    padding-left: 15px;
    padding-top: 15px;
    margin-bottom: 40px;
}
.btn-div{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60%;
    height: 20%;
}
.btn{
    width: 170px;
    height: 60px;
    border-style: none;
    border-radius: 29px;
    border: 3px solid #0080ff;
    font-family: Roboto, sans-serif;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    text-align: center;
    color: #0080ff;
}
.btn:hover{
    background-color: #0080ff;
    color: #fff;
    transition: 0.3s;
}
.btn:focus{
    outline: none;
    background-color: #0080ff;
    color: #fff;
    transition: 0.3s;
}
@media only screen and (max-width: 1190px){
    .tittle-container h1{
        font-size: 30px;
    }
    .form-input{
        width: 80%;
    }
    .textarea{
        width: 80%;
    }
    .btn-div{
        width: 90%;
    }
}
@media only screen and (max-width: 890px){
    .tittle-container h1{
        font-size: 28px;
    }
    .form-input{
        flex-direction: column;
        height: 30%;
    }
    .input-email{
        margin: 0px;
    }
    .form-input-item label{
        margin-top: 20px;
    }
    .form-input{
        width: 90%;
    }
    .textarea{
        width: 90%;
    }
    .btn-div{
        width: 90%;
    }
}
@media only screen and (max-width: 700px){
    .tittle-container h1{
        font-size: 25px;
    } 
    .form-input{
        height: 50%;
    }
}
@media only screen and (max-width: 590px){
    .tittle-container h1{
        font-size: 22px;
    }
}

@keyframes translate-down {
    0% { 
        -webkit-transform: translateY(-10px); 
        transform: translateY(-10px); 
    }
    100% { 
        -webkit-transform: translateY(0); 
        transform: translateY(0); 
    } 
}

@keyframes translate-up{ 
    0% { 
        -webkit-transform: translateY(-195px); 
        transform: translateY(-195px); 
    }
    100% {
        -webkit-transform: translateY(0); 
        transform: translateY(0); 
    } 
}


